<template>
    <section class="section-2-container">
        <div @mouseover="transitionIntoVideo()" @mouseleave="hover = false" class="section-2-title-container">
            <h1 class="section-2-title" id="section-2-title">What do we do?</h1>
            <div class="video-player" id="video-player">
                <video v-if="width >= 100 && width <= 373" id="player" muted>
                    <source id="simplify-video" src="../assets/animation/simplify-360_780.mp4" type="video/mp4">
                    Your browser does not support the video tag.
                </video>
                <video v-else-if="width >= 374 && width <= 412" id="player" muted>
                    <source id="simplify-video" src="../assets/animation/simplify-375_812.mp4" type="video/mp4">
                    Your browser does not support the video tag.
                </video>
                <video v-else-if="width >= 413 && width <= 709" id="player" muted>
                    <source id="simplify-video" src="../assets/animation/simplify-414_896.mp4" type="video/mp4">
                    Your browser does not support the video tag.
                </video>
                <video v-else-if="width >= 800 && width <= 1367" id="player" muted>
                    <source id="simplify-video" src="../assets/animation/simplify-1366_768.mp4" type="video/mp4">
                    Your browser does not support the video tag.
                </video>
                <video v-else-if="width >= 1439 && width <= 1441" id="player" muted>
                    <source id="simplify-video" src="../assets/animation/simplify-1440_900.mp4" type="video/mp4">
                    Your browser does not support the video tag.
                </video>
                <video v-else-if="width >= 1535 && width <= 1918" id="player" muted>
                    <source id="simplify-video" src="../assets/animation/simplify-1536_864.mp4" type="video/mp4">
                    Your browser does not support the video tag.
                </video>
                <video v-else-if="width >= 1919" id="player" muted>
                    <source id="simplify-video" src="../assets/animation/simplify-1920_1080.mp4" type="video/mp4">
                    Your browser does not support the video tag.
                </video>
            </div>
            <p class="section-2-paragraph" id="section-2-paragraph">We create the website that will make your <br>Clients go : "I'm IN!"</p>
        </div>
    </section>
</template>

<script>
    export default {
        name: 'SectionTwo',
        components: {
        },
        data() {
            return{
                width: 0
            };
        },
        beforeMount() {
            this.width = (window.innerWidth > 0) ? window.innerWidth : screen.width;
            console.log(this.width);
            // if(width <= 1367 && width >= 1366 ){
            //     let video = document.getElementById("player");
            //     console.log(document.getElementById('player'));
            //     video.src="../assets/animation/simplify-375_667.mp4";
            // }
        },
        methods: {
            transitionIntoVideo(){
                let element = document.getElementById("section-2-title");
                element.classList.add("section-title-fadeout");
                setTimeout(
                    function(){
                        let element = document.getElementById("video-player");
                        element.classList.add("video-player-fadein");
                        let player= document.getElementById("player");
                        player.play();
                    }, 500
                );
                setTimeout(
                    function(){
                        let element = document.getElementById("video-player");
                        element.classList.add("video-player-fadeout");
                    }, 5500
                );
                setTimeout(
                    function(){
                        let element = document.getElementById("section-2-paragraph");
                        element.classList.add("section-paragraph-fadein");
                    }, 6000
                );
            }
        }
    }
</script>

<style scoped>
    .section-2-container {
        height: 100vh;    
        width: 100%;
        display: flex; 
        align-items: center;
    }
    .section-2-title-container {
        height: 100%;
        width: 100%;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        overflow: hidden;
    }
    .section-2-title {
        font: Nunito;
        font-size: 180px;
        font-weight: 900;
        transition: transform .5s ease-out, opacity .5s ease-in; 
    }
    .section-title-fadeout {
        transform: translate(0px,-20px);
        opacity: 0;
    }
    .section-2-paragraph {
        font-size: 80px;
        font-weight: Regular;
        position: absolute;
        z-index: 2;
        opacity: 0;
        transform: translate(0px,-20px);
        transition: transform .5s ease-in, opacity .5s ease-out; 
    }
    .section-paragraph-fadein {
        opacity: 1;
        transform: translate(0px,0px);
    }
    .video-player{
        position: absolute;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1;
        opacity: 0;
        transition: opacity .5s ease-out; 
    }
    .video-player-fadein{
        opacity: 1;
    }
    .video-player-fadeout{
        opacity: 0;
    }

    @media only screen and ( max-width: 1536px ){
        .section-2-title {
            font-size: 160px;
        }

        .section-2-paragraph {
            font-size: 70px;
        }
    }

    @media only screen and ( max-width: 1366px ){
        .section-2-title {
            font-size: 140px;
        }

        .section-2-paragraph {
            font-size: 60px;
        }
    }

    @media only screen and ( max-width: 768px ){
        .section-2-title {
            font-size: 80px;
        }

        .section-2-paragraph {
            font-size: 30px;
        }
    }

    @media only screen and ( max-width: 414px ){
        .section-2-title {
            font-size: 40px;
        }

        .section-2-paragraph {
            font-size: 16px;
        }
    }



</style>
