<template>
    <section class="section-4-container">
        <div class="section-4-form-container">
            <h1 class="section-4-form-title">Get in Touch</h1>  
            <form @submit.prevent="handleSubmit">
                <div class="section-4-inner-form-container">
                    <div class="section-4-inner-top">
                        <div class="section-4-inner-left">
                            <div class="form-group" :class="{ error: v$.form.name.$errors.length }">
                                <label>Name:</label>
                                <input type="text" name="name" placeholder="Your name" v-model="v$.form.name.$model">
                                <div class="input-errors" v-for="(error, index) of v$.form.name.$errors" :key="index">
                                    <div class="error-msg">{{ error.$message }}</div>
                                </div>
                            </div>
                            <div class="form-group" :class="{ error: v$.form.phone.$errors.length }">
                                <label>Phone Number:</label>
                                <input type="tel" name="phone" placeholder="Your phone number" v-model="v$.form.phone.$model">
                                <div class="input-errors" v-for="(error, index) of v$.form.phone.$errors" :key="index">
                                    <div class="error-msg">{{ error.$message }}</div>
                                </div>
                            </div>
                        </div>
                        <div class="section-4-inner-right">
                            <div class="form-group" :class="{ error: v$.form.email.$errors.length }">
                                <label>Email:</label>
                                <input type="email" name="email" placeholder="Your email" v-model="v$.form.email.$model">
                                <div class="input-errors" v-for="(error, index) of v$.form.email.$errors" :key="index">
                                    <div class="error-msg">{{ error.$message }}</div>
                                </div>
                            </div>
                            <div class="form-group">
                                <label>How did you hear about us:</label>
                                <input type="text" name="affiliation" placeholder="How did you hear about us?" v-model="v$.form.affiliation.$model">
                            </div>
                        </div>
                    </div>
                    <div class="section-4-inner-bottom">
                        <div class="form-group" :class="{ error: v$.form.message.$errors.length }">
                            <label>Message:</label>
                            <textarea type="text" name="message" rows="4" placeholder="Brief description of your needs" v-model="v$.form.message.$model"></textarea>
                                <div class="input-errors" v-for="(error, index) of v$.form.message.$errors" :key="index">
                                    <div class="error-msg">{{ error.$message }}</div>
                                </div>
                        </div>
                    </div>
                </div>
                <div class="submit-container">
                    <button :disabled="v$.form.$invalid" class="send-button">SEND</button>
                    <p class="submit-phone-contact">Or call us on the number +216 53 582 789</p>
                </div>
            </form> 
        </div>
    </section>
</template>

<script>
    import gsap from 'gsap';
    import ScrollTrigger from 'gsap/ScrollTrigger';
    import emailjs from 'emailjs-com';
    import useVuelidate from '@vuelidate/core';
    import {required, email} from '@vuelidate/validators';

    gsap.registerPlugin(ScrollTrigger);

    export function validName(name) {
        let validNamePattern = new RegExp("^[a-zA-Z]+(?:[-'\\s][a-zA-Z]+)*$");

        if (validNamePattern.test(name)){
            return true;
        }

        return false;
    }

    export function validPhonenumber(number) {
        let validPhonePattern = new RegExp("((([+]|00)216)([2579][0-9]{7}|(3[012]|4[01]|8[0128])[0-9]{6}|42[16][0-9]{5}))|^$");
        let validPhonePatternBis = new RegExp("(([2579][0-9]{7}|(3[012]|4[01]|8[0128])[0-9]{6}|42[16][0-9]{5}))|^$"); 

        if (validPhonePattern.test(number) || validPhonePatternBis.test(number)){
            return true;
        }

        return false;
    }

    export default {
        name: 'SectionFour',
        components: {
        },
        setup() {
            return { v$ : useVuelidate() }
        },
        data() {
            return {
                form: {
                    name: '',
                    phone: '',
                    email: '',
                    affiliation: '',
                    message: '',
                }
            }
        },
        methods: {
            handleSubmit(e) {
                try {
                    let buttonHTML = document.getElementsByClassName("send-button")[0];
                    buttonHTML.classList.add("loading");
                    buttonHTML.disabled = true;
                    console.log("loading"),

                    emailjs.sendForm('service_prodigytest', 'template_q9b6hdn', e.target,
                    'user_7FZcsWT6gakXWvxE9Yu3P', {
                    name: this.name,
                    email: this.email,
                    message: this.message,
                    phone: this.phone,
                    affiliation: this.affiliation,
                    })
                    .then(
                        function(){
                            console.log("sent");
                            let buttonHTML = document.getElementsByClassName("send-button")[0];
                            buttonHTML.classList.remove("loading");
                            buttonHTML.classList.add("clicked");
                            buttonHTML.innerHTML = "SENT";
                        }
                    );

                } catch(error) {
                    console.log({error});
                }
                // Reset form field
                this.name = '';
                this.email = '';
                this.message = '';
                this.phone = '';
                this.affiliation = '';
            }
        },
        validations(){
            return{
                    name: '',
                    phone: '',
                    email: '',
                    affiliation: '',
                    message: '',
                form : {
                    name: {
                        required, name_validation: {
                            $validator: validName,
                            $message: 'Invalid Name. Valid name only contain letters, dashes (-) and spaces'
                        }
                    },
                    phone: {
                        phone_validation: {
                            $validator: validPhonenumber,
                            $message: 'Valid phone number must be Tunisian.'
                        }
                    },
                    email : {
                        required, email
                    },
                    affiliation: {
                        
                    },
                    message: {
                        required
                    },
                }

                
            }
        },
        mounted() {
            gsap.from("form",{
                scrollTrigger: {
                    trigger: "form",
                    start: "40% bottom",
                    toggleActions: "play none none none",
                },
                ease: "power1",
                opacity: 0,
                translateY: 90,
                duration: 1,
            })
        }
    }
</script>

<style scoped>

/* Section Container */

    .section-4-container {
        height: 100vh;    
        width: 100%;
        display: flex; 
        justify-content: center;
        align-items: center; 
    }

/* Form container */

    .section-4-form-container {
        height: 100%;
        width: 70%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }

/* H1 Title */

    .section-4-form-title {
        font-weight: 700;
        font-size: 80px;
        color: #12263F;
        text-align: center;
    }

/* Form */

    form {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }

/* Inner form container */

    .section-4-inner-form-container{
        width: 100%;
        display: flex;
        flex-direction: column;
    }

/* Top half of the inner form container */

    .section-4-inner-top {
        /* height: 60%; */
        width: 100%;
        display: flex;
        justify-content: space-between;
    }

/* Bottom half of the inner form container */

    .section-4-inner-bottom  {
        height: 40%;
        width: 100%;
        
    }

/* Left side of the top half of the inner form container */

    .section-4-inner-left{
        height: 100%;
        width: 45%;
    }

/* Right side of the top half of the inner form container */

    .section-4-inner-right{
        height: 100%;
        width: 45%;
    }

/* Last part of the form (send button and footer) */

    .submit-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 10%
    }

    .submit-phone-contact {
        font-weight: 700;
        font-size: 30px;
        color: #8b8b8e;
    }

    .send-button {
        font-weight: 900;
        font-size: 24px;
        padding: 9px 80px;
        background-color: #2E4FEB;
        color: #FFF;
        transition: padding ease .5s, font-size ease .5s, color ease .5s, background-color ease .5s, transform ease .5s, border ease .5s, width ease .5s, height ease .5s, border-radius ease .5s;
        /* transition: color .15s ease-out, background-color .15s ease-out, transform .15s ease-out; */
    }

    .send-button.loading{
        padding: 0;
        border: 1px solid #2E4FEB; 
        /* border: 1px solid #2E4FEB;  */
        background-color: #F5F7FA;
        font-size: 0;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        border-left-color: transparent;
        animation: rotate 1.4s ease .5s infinite;
    }
    @keyframes rotate {
        0%{
            transform: rotate(360deg);
        }
    }

    .send-button.clicked {
        background-color: #FFF;
        color: #0CCA98;
    }

    .send-button:hover {
        background-color: #0CCA98;
        color: #F5F7FA;
        /* background-color: #F5F7FA;
        color : #2E4FEB; */
        /* transform: scale(1.5); */
    }

/* Form groups of label & input */

    .form-group {
        margin: 20px 0;
    }

    label {
        font-weight: 700;
        color: black;
        font-size: 36px;
    }

    input {
        display: block;
        padding: 10px 6px;
        width: 100%;
        border: none;
        border-bottom: 1px solid #000;
        background-color: #F5F7FA;
    }

    textarea {
        display: block;
        padding: 10px 6px;
        width: 100%;
        border: none;
        border-bottom: 1px solid #000;
        background-color: #F5F7FA;
    }

    @media only screen and ( max-width: 1440px ){

        .section-4-form-title {
            font-size: 70px;
        }

        .form-group {
            margin: 18px 0;
        }

        label {
            font-size: 30px;
        }

        input {
            padding: 9px 5px;
        }

        textarea {
            padding: 9px 5px;
        }

        .submit-phone-contact {
            font-size: 26px;
        }

        .send-button {
            font-size: 20px;
            padding: 9px 70px;
        }
    }

    @media only screen and ( max-width: 1024px ){

        label {
            font-size: 26px;
        }

        input {
            padding: 12px 6px;
            /* border-bottom: none; */
        }

        textarea {
            padding: 12px 6px;
            /* border-bottom: none; */
        }

        .submit-phone-contact {
            font-size: 20px;
        }

        .send-button {
            font-size: 20px;
            padding: 9px 70px;
        }

        .section-4-inner-top {
            width: 100%;
            display: flex;
            justify-content: flex-start;
            flex-direction: column;
        }

        .section-4-inner-left{
            margin-bottom: -8px;
            height: 100%;
            width: 100%;
        }

        .section-4-inner-right{
            margin-bottom: -8px;
            height: 100%;
            width: 100%;
        }
    }

    @media only screen and ( max-width: 768px ){

        .submit-phone-contact {
            font-size: 16px;
        }

        .send-button {
            font-size: 20px;
            padding: 9px 50px;
        }

        input {
            border-bottom: none;
        }

        textarea {
            border-bottom: none;
        }
    }

    @media only screen and ( max-width: 414px ){

        .submit-container{
            flex-direction: column;
        }

        .submit-phone-contact {
            font-size: 12px;
            text-align: center;
        }

        .send-button {
            font-size: 16px;
            padding: 9px 50px;
        }

        .section-4-form-title {
            font-size: 40px;
        }

        .form-group {
            margin: 14px 0;
        }

        label {
            font-size: 20px;
        }

        input {
            padding: 10px 5px;
        }

        textarea {
            padding: 10px 5px;
        }

        form {
            justify-content: space-evenly;
        }
    }

    @media only screen and ( max-width: 360px ){

        .submit-container{
            height: 11%;
        }

        .submit-phone-contact {
            font-size: 12px;
            text-align: center;
        }

        .send-button {
            font-size: 16px;
            padding: 6px 60px;
        }

        .section-4-form-title {
            font-size: 36px;
        }

        .form-group {
            margin: 10px 0;
        }

        label {
            font-size: 16px;
        }

        input {
            padding: 10px 5px;
        }

        textarea {
            padding: 10px 5px;
        }
    }

</style>
